:root {
  --font-body: "Red Hat Display", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --verypaleblue: hsl(225, 100%, 98%);
  --desaturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-image: url(data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%221440%22%20height%3D%22437%22%3E%3Cpath%20fill%3D%22%237280a7%22%20fill-rule%3D%22evenodd%22%20d%3D%22M0%20349.974c218.558%20116.035%20460.05%20116.035%20724.475%200s502.933-116.035%20715.525%200V0H0v349.974z%22%2F%3E%3C%2Fsvg%3E);
  background-color: var(--paleblue);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center top;
}
@media (max-width: 23.4375em) {
  body {
    background-image: none;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body p {
  font-size: 16px;
}

.container {
  padding: 0 1.5rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: #3e52a3;
}

.card-container {
  background-color: #fff;
  max-width: 28.125rem;
  height: 44rem;
  margin: 5rem auto;
  border-radius: 25px;
  font-family: var(--font-body);
}
@media (max-width: 23.4375em) {
  .card-container {
    margin: 4rem 1.5rem;
    height: 42rem;
  }
}
.card-image {
  background-image: url(../../assets/illustration-hero.svg);
  height: 220px;
  border-radius: 25px 25px 0 0px;
  overflow: hidden;
}
.card-header {
  text-align: center;
  font-size: 2rem;
  font-weight: 900;
  margin-top: 2rem;
  margin-bottom: 1rem;
  color: var(--darkblue);
}
@media (max-width: 23.4375em) {
  .card-header {
    font-size: 1.5rem;
  }
}
.card-description {
  text-align: center;
  margin: 0 4rem;
  color: var(--darkblue);
  opacity: 0.6;
}
@media (max-width: 23.4375em) {
  .card-description {
    margin: 0 1.6rem;
  }
}
.card-plan {
  display: flex;
  justify-content: space-around;
  margin: 2rem 3rem;
  height: 7rem;
  align-items: center;
  background-color: var(--paleblue);
  border-radius: 10px;
}
@media (max-width: 23.4375em) {
  .card-plan {
    margin: 2rem 1.5rem;
  }
}
.card-plan-details {
  font-size: 1.4rem;
}
@media (max-width: 23.4375em) {
  .card-plan-details {
    font-size: 1.2rem;
  }
}
.card-plan-details-freq {
  font-weight: 700;
}
.card-plan-details-price {
  color: var(--darkblue);
  opacity: 0.6;
  font-weight: 500;
}
.card-plan a {
  font-size: 1rem;
  color: var(--brightblue);
  text-decoration: underline;
  font-weight: 700;
}
@media (max-width: 23.4375em) {
  .card-plan a {
    font-size: 0.9rem;
  }
}
.card-plan a:hover {
  opacity: 0.6;
  text-decoration: none;
}
.card-button {
  display: block;
  margin: 1rem auto;
  padding: 1rem 6rem;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
  font-weight: 700;
  font-family: var(--font-body);
  background-color: var(--brightblue);
  color: #fff;
  box-shadow: 1px 10px 30px var(--brightblue);
  cursor: pointer;
}
@media (max-width: 23.4375em) {
  .card-button {
    margin: 1rem auto;
    padding: 1rem 4rem;
  }
}
.card-button:hover {
  opacity: 0.6;
}
.card a.card-link {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
  color: var(--darkblue);
  font-weight: 900;
  opacity: 0.6;
}
.card a.card-link:hover {
  opacity: 1;
}